import React, { useState, useEffect } from "react";
import { useStaticConfigJSON } from "../../../plugins/remote-content/src/components/ppm/static-content/useStaticConfig";
import { SITE_CONTENT } from "../../contexts/content";

/** tiny safety net */
const safeParse = (value: any, label: string) => {
  try {
    if (!value) return null;
    if (typeof value === "object") return value;
    return JSON.parse(value);
  } catch (e) {
    console.warn(` Resolver skipped ${label}`, e);
    return null;
  }
};

// NEW: what YOU actually want
const hasAnyData = (obj: any) => obj && typeof obj === "object";

// separate check: does it look like full site content?
const hasFullSiteShape = (obj: any) =>
  obj &&
  typeof obj === "object" &&
  typeof obj.global === "object" &&
  typeof obj.pages === "object";

// --- Layer B: remote static JSON ---
const useStaticConfigLayer = () => {
  if (typeof window === "undefined") return null;

  const remote = useStaticConfigJSON(
    "https://mtcmarketing.github.io/plugin--remote-content--vien-phat-hoc-v3/static-content/content.json"
  );

  if (!remote) return null;

  const parsed = safeParse(remote, "static-config");
  if (!parsed) return null;

  return {
    data: parsed,
    version: parsed?.version ?? null,   // <-- CORRECTED
    source: "static-config",
    raw: remote
  };
};

// --- Layer C: CMS Drafts ---
const getCmsDraft = () => {
  if (typeof window === "undefined") return null;

  const raw = localStorage.getItem("sbu-cms-draft");
  if (!raw) return null;

  const parsed = safeParse(raw, "cms-draft");
  if (!parsed) return null;

  return {
    data: parsed,
    version: parsed?.version ?? null,
    source: "cms-draft",
    raw
  };
};

// --- Layer D: Local Published ---
const getLocalPublished = () => {
  if (typeof window === "undefined") return null;

  const raw = localStorage.getItem("sbu-cms-data");
  if (!raw) return null;

  const parsed = safeParse(raw, "local-published");
  if (!parsed) return null;

  return {
    data: parsed,
    version: parsed?.version ?? null,
    source: "local-published",
    raw
  };
};

// --- Layer E: build baseline ---
const getBuildBaseline = () => ({
  data: SITE_CONTENT,
  version: 0,
  source: "build-baseline",
  raw: SITE_CONTENT
});

export const useResolvedContent = (fallback: any, ignoreDraft: boolean = false, refreshKey: number = 0) => {
  const [storageTick, setStorageTick] = useState(0);

  // Sync across tabs (MTC-BDPQ-028)
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const handleStorage = (e: StorageEvent) => {
      if (e.key === "sbu-cms-draft" || e.key === "sbu-cms-data") {
        console.log(" Storage change detected, re-resolving...");
        // Clear local memo to force fresh read
        const memoKey = `_resolved_memo_${ignoreDraft ? 'live' : 'draft'}`;
        delete (window as any)[memoKey];
        setStorageTick(t => t + 1);
      }
    };
    window.addEventListener('storage', handleStorage);
    return () => window.removeEventListener('storage', handleStorage);
  }, [ignoreDraft]);

  if (typeof window === "undefined") return fallback;

  // ============================================================
  // PREVIEW MODE CHECK
  // ============================================================
  const params = new URLSearchParams(window.location.search);
  const isPreview = params.get('preview') === 'true';

  const draft = getCmsDraft();

  // If we are in preview mode AND NOT ignoring the draft, the draft ALWAYS wins
  if (isPreview && !ignoreDraft && draft && hasFullSiteShape(draft.data)) {
    return draft.data;
  }

  const staticCfg = useStaticConfigLayer();
  const local = getLocalPublished();
  const baseline = getBuildBaseline();

  const staticHasFullSite = hasFullSiteShape(staticCfg?.data);
  const draftHasFullSite = hasFullSiteShape(draft?.data);
  const localHasFullSite = hasFullSiteShape(local?.data);
  const baselineHasFullSite = hasFullSiteShape(baseline.data);

  // ---- Pick best FULL SITE source (Normal Flow) ----
  const candidates: any[] = [];

  if (staticCfg && staticHasFullSite) candidates.push(staticCfg);

  // Only add draft if NOT ignoring it
  if (!ignoreDraft && draft && draftHasFullSite) {
    candidates.push(draft);
  }

  if (local && localHasFullSite) candidates.push(local);
  if (baselineHasFullSite) candidates.push(baseline);

  if (candidates.length === 0) {
    return fallback;
  }

  candidates.sort((a, b) => {
    const av = a.version ?? -Infinity;
    const bv = b.version ?? -Infinity;
    return bv - av;
  });

  const winner = candidates[0];

  // ============================================================
  // IDENTITY STABILITY (MTC-BDPQ-026)
  // ============================================================
  const lastKey = `${winner.source}-${winner.version}-${ignoreDraft}-${isPreview}-${refreshKey}-${storageTick}`;
  const memoKey = `_resolved_memo_${ignoreDraft ? 'live' : 'draft'}`;

  const win = window as any;
  if (win[memoKey]?.key === lastKey) {
    return win[memoKey].data;
  }

  console.groupCollapsed(" RESOLVED WINNER" + (ignoreDraft ? " [LIVE]" : " [DRAFT]"));
  console.log({
    source: winner.source,
    version: winner.version,
    refreshKey,
    storageTick
  });
  console.groupEnd();

  // Attach metadata as non-enumerable properties
  const finalData = winner.data;
  if (finalData && typeof finalData === 'object') {
    Object.defineProperty(finalData, '_remoteVersion', {
      value: staticCfg?.version ?? null,
      enumerable: false,
      configurable: true
    });
    Object.defineProperty(finalData, '_draftVersion', {
      value: draft?.version ?? null,
      enumerable: false,
      configurable: true
    });
  }

  // Cache it globally
  win[memoKey] = {
    key: lastKey,
    data: finalData
  };

  return finalData;
};
