import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react'

// CMS DATA TYPE
// ========================

export type CMSData = {
  global: {
    headerContent: HeaderContent
    footerContent: FooterContent
  }
  pages: {
    home: {
      header: HeaderData
      upcomingEventContent: UpcomingEventContent
      newsContent: NewsContent
      programsContent: ProgramsContent
      supportContent: SupportContent
      scriptureContent: ScriptureContent
    }
    page2: {
      header: HeaderData
      loiTuBachContent: LoiTuBachContent
      bonDaiTongChiContent: BonDaiTongChiContent
      banNhanSuContent: BanNhanSuContent
      ghiDanhNhapHocContent: GhiDanhNhapHocContent
    }
    page3: {
      header: HeaderData
      khongGianThienContent: KhongGianThienContent
      khongGianThien12Content: KhongGianThien12Content
      ghiDanhThienContent: GhiDanhThienContent
    }
    page4: {
      header: HeaderData
      chuongTrinhSoTrungDangContent: ChuongTrinhSoTrungDangContent
      nienKhoaContent: Page4NienKhoaContent
      nienKhoa2022Content: Page4aNienKhoa2022Content
      nienKhoa2019Content: Page4bNienKhoa2019Content
    }
    page5: {
      header: HeaderData
      chuongTrinhCaoDangContent: ChuongTrinhCaoDangContent
      caoDangNienKhoaContent: Page5cCaoDangNienKhoaContent
      caoDang2022Content: Page5eChuongTrinhCaoDangContent
      caoDang2019Content: Page5fChuongTrinhCaoDangContent
    }
    page6: {
      header: HeaderData
      chuyenKhoaATyDamContent: Page6aChuyenKhoaATyDamContent
      nienKhoaContent: Page6bNienKhoaContent
      programSectionEFContent: ProgramSectionEFContent
    }
  }
  version?: number
}

// ========================
// CONTEXT TYPE
// ========================

type CMSContextType = {
  cmsData: CMSData
  displayData: CMSData
  hasDraftChanges: boolean
  currentViewMode: 'draft' | 'original'
  switchViewMode: (mode: 'draft' | 'original') => void
  resetCMSData: () => void
  exportCMSData: () => string
  importCMSData: (jsonData: string) => boolean
  updateHeaderContent: (headerContent: Partial<HeaderContent>) => void
  updateFooterContent: (footerContent: Partial<FooterContent>) => void
  updatePageHeader: (pageKey: keyof CMSData['pages'], headerData: Partial<HeaderData>) => void
  updateUpcomingEventContent: (content: Partial<UpcomingEventContent>) => void
  updateNewsContent: (newsContent: Partial<NewsContent>) => void
  updatePrograms: (programsContent: Partial<ProgramsContent>) => void
  updateSupportContent: (supportContent: Partial<SupportContent>) => void
  updateScriptureContent: (scriptureContent: Partial<ScriptureContent>) => void
  updateLoiTuBachContent: (content: Partial<LoiTuBachContent>) => void
  updateBonDaiTongChiContent: (content: Partial<BonDaiTongChiContent>) => void
  updateBanNhanSuContent: (content: Partial<BanNhanSuContent>) => void
  updateGhiDanhNhapHocContent: (content: Partial<GhiDanhNhapHocContent>) => void
  updateKhongGianThienContent: (content: Partial<KhongGianThienContent>) => void
  updateKhongGianThien12Content: (content: Partial<KhongGianThien12Content>) => void
  updateGhiDanhThienContent: (content: Partial<GhiDanhThienContent>) => void
  updateChuongTrinhSoTrungDangContent: (content: Partial<ChuongTrinhSoTrungDangContent>) => void
  updateNienKhoaContent: (content: Partial<Page4NienKhoaContent>) => void
  updateNienKhoa2022Content: (content: Partial<Page4aNienKhoa2022Content>) => void
  updateNienKhoa2019Content: (content: Partial<Page4bNienKhoa2019Content>) => void
  updateChuongTrinhCaoDangContent: (content: Partial<ChuongTrinhCaoDangContent>) => void
  updateCaoDangNienKhoaContent: (content: Partial<Page5cCaoDangNienKhoaContent>) => void
  updateCaoDang2022Content: (content: Partial<Page5eChuongTrinhCaoDangContent>) => void
  updateCaoDang2019Content: (content: Partial<Page5fChuongTrinhCaoDangContent>) => void
  updateChuyenKhoaATyDamContent: (content: Partial<Page6aChuyenKhoaATyDamContent>) => void
  updateNienKhoaContent6: (content: Partial<Page6bNienKhoaContent>) => void
  updateProgramSectionEFContent: (content: Partial<ProgramSectionEFContent>) => void
  saveAutosave: () => void
  saveDraft: () => void
  publishCMSData: () => void
  discardDraft: () => void
  hasUnsavedChanges: boolean
  resolvedContent: CMSData | null
  remoteVersion: number | null
  draftVersion: number | null
  isSuperAdmin: boolean
  isSuperAdminMode: boolean
  toggleSuperAdminMode: () => void
}

// ========================
// DEFAULT CONTENT
// ========================

import { SITE_CONTENT } from './content'
import {
  BanNhanSuContent, BonDaiTongChiContent, ChuongTrinhCaoDangContent,
  ChuongTrinhSoTrungDangContent, FooterContent, GhiDanhNhapHocContent,
  GhiDanhThienContent, HeaderContent, HeaderData, KhongGianThien12Content,
  KhongGianThienContent, LoiTuBachContent, NewsContent,
  Page4aNienKhoa2022Content, Page4bNienKhoa2019Content, Page4NienKhoaContent,
  Page5cCaoDangNienKhoaContent, Page5eChuongTrinhCaoDangContent,
  Page5fChuongTrinhCaoDangContent, Page6aChuyenKhoaATyDamContent,
  Page6bNienKhoaContent, ProgramsContent, ProgramSectionEFContent,
  ScriptureContent, SupportContent, UpcomingEventContent
} from './types'
import { useResolvedContent } from '../ppm/hooks/useResolvedContent'

const defaultCMSData: CMSData = SITE_CONTENT as unknown as CMSData

// ========================
// CONTEXT
// ========================

const CMSContext = createContext<CMSContextType | undefined>(undefined)

// ========================
// STORAGE KEYS
// ========================

const CMS_STORAGE_KEY = 'sbu-cms-data'       // Local published (after "Publish")
const CMS_DRAFT_KEY = 'sbu-cms-draft'        // User's saved draft (after "Save Draft")
const CMS_AUTOSAVE_KEY = 'sbu-cms-autosave'  // Background backup only

// ========================
// HELPER
// ========================

export const hasActiveDraft = () => {
  if (typeof window === 'undefined') return false

  // A draft exists ONLY if sbu-cms-draft exists
  // Autosave is just a backup and doesn't count as a "real" draft
  const draft = localStorage.getItem(CMS_DRAFT_KEY)
  return !!draft
}

// ========================
// PROVIDER
// ========================

export const CMSProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [refreshKey, setRefreshKey] = useState(0)
  const [remoteVersion, setRemoteVersion] = useState<number | null>(null)
  const [draftVersion, setDraftVersion] = useState<number | null>(null)

  // Super Admin Logic
  const isSuperAdmin = true; // Hardcoded for now
  const CMS_SUPER_ADMIN_MODE_KEY = 'sbu-cms-super-admin-mode'

  const [isSuperAdminMode, setIsSuperAdminMode] = useState(() => {
    if (typeof window !== 'undefined') {
      return localStorage.getItem(CMS_SUPER_ADMIN_MODE_KEY) === 'true'
    }
    return false
  })

  const toggleSuperAdminMode = () => {
    setIsSuperAdminMode(prev => {
      const newValue = !prev
      if (typeof window !== 'undefined') {
        localStorage.setItem(CMS_SUPER_ADMIN_MODE_KEY, String(newValue))
      }
      return newValue
    })
  }

  // ==================================================================
  // RESOLVE INITIAL CONTENT (Nate's Model)
  // ==================================================================
  const resolvedContent = useResolvedContent(defaultCMSData, false, refreshKey)
  const liveContent = useResolvedContent(defaultCMSData, true, refreshKey) // true = ignoreDraft

  const [cmsData, setCmsData] = useState<CMSData>(defaultCMSData)
  const [initialized, setInitialized] = useState(false)

  // Sync cmsData with resolvedContent ONLY ONCE on mount
  useEffect(() => {
    if (!initialized && resolvedContent) {
      console.log('[CMS][INIT] Initializing cmsData from resolvedContent v' + (resolvedContent as any).version)
      setCmsData(resolvedContent)
      setInitialized(true)
    }

    // Extract version metadata
    setRemoteVersion((resolvedContent as any)._remoteVersion ?? null)
    setDraftVersion((resolvedContent as any)._draftVersion ?? null)
  }, [resolvedContent, initialized])

  // DERIVED STATE (MTC-BDPQ-027)
  const unsavedInfo = React.useMemo(() => {
    if (typeof window === 'undefined') return { unsaved: false, draftChanges: false }

    const currentStr = JSON.stringify(cmsData)
    const draftStr = localStorage.getItem(CMS_DRAFT_KEY)
    const resolvedStr = JSON.stringify(resolvedContent)

    // Draft Changes: Are we currently synced EXACTLY with the draft? (i.e. no changes since save)
    const matchesDraft = draftStr ? (currentStr === draftStr) : false
    // Unsaved: Are we DIFFERENT from our baseline (draft if exists, else remote)?
    const unsaved = draftStr ? (currentStr !== draftStr) : (currentStr !== resolvedStr)

    return { unsaved, draftChanges: matchesDraft }
  }, [cmsData, resolvedContent])

  const hasUnsavedChanges = unsavedInfo.unsaved
  const hasDraftChanges = hasActiveDraft() // Simple check: does a draft EXIST?

  // ==================================================================
  // VIEW MODE & DISPLAY DATA
  // ==================================================================
  const [currentViewMode, setCurrentViewMode] = useState<'draft' | 'original'>('draft')
  const displayData = currentViewMode === 'original' ? liveContent : cmsData

  const switchViewMode = (mode: 'draft' | 'original') => {
    console.log('[CMS][VIEW MODE]', mode)
    setCurrentViewMode(mode)
  }

  // ==================================================================
  // STORAGE ACTIONS
  // ==================================================================
  const saveAutosave = () => {
    if (typeof window === 'undefined') return
    localStorage.setItem(CMS_AUTOSAVE_KEY, JSON.stringify(cmsData))
    console.log('[CMS][AUTOSAVE] background backup saved')
  }

  // Autosave Effect
  useEffect(() => {
    if (hasUnsavedChanges) {
      const timer = setTimeout(() => {
        saveAutosave()
      }, 5000)
      return () => clearTimeout(timer)
    }
  }, [cmsData, hasUnsavedChanges])

  const saveDraft = () => {
    if (typeof window === 'undefined') return
    const dataObj: any = JSON.parse(JSON.stringify(cmsData))
    const winnerVersion = (resolvedContent as any).version || 0
    dataObj.version = winnerVersion + 1
    localStorage.setItem(CMS_DRAFT_KEY, JSON.stringify(dataObj))
    setCmsData(dataObj)
  }

  const publishCMSData = () => {
    if (typeof window === 'undefined') return
    const dataObj: any = JSON.parse(JSON.stringify(cmsData))
    const dVersion = dataObj.version || 0
    dataObj.version = dVersion + 1
    localStorage.setItem(CMS_STORAGE_KEY, JSON.stringify(dataObj))
    localStorage.removeItem(CMS_DRAFT_KEY)
    localStorage.removeItem(CMS_AUTOSAVE_KEY)

    // Trigger refresh (MTC-BDPQ-030)
    if (typeof window !== 'undefined') {
      delete (window as any)._resolved_memo_draft;
      delete (window as any)._resolved_memo_live;
    }
    setRefreshKey(p => p + 1)
    setInitialized(false)
  }

  const discardDraft = () => {
    if (typeof window === 'undefined') return
    localStorage.removeItem(CMS_DRAFT_KEY)
    localStorage.removeItem(CMS_AUTOSAVE_KEY)

    // Clear hook memo (MTC-BDPQ-029)
    if (typeof window !== 'undefined') {
      delete (window as any)._resolved_memo_draft;
      delete (window as any)._resolved_memo_live;
    }

    setRefreshKey(p => p + 1)
    setInitialized(false) // Force re-sync of editor data
  }

  const resetCMSData = () => {
    if (typeof window === 'undefined') return
    if (!confirm('WARNING: This will wipe ALL CMS data (drafts, published, local) and reset to build defaults. Continue?')) return
    localStorage.removeItem(CMS_STORAGE_KEY)
    localStorage.removeItem(CMS_DRAFT_KEY)
    localStorage.removeItem(CMS_AUTOSAVE_KEY)
    window.location.reload()
  }

  const exportCMSData = () => JSON.stringify(cmsData, null, 2)

  const importCMSData = (jsonData: string) => {
    try {
      const parsedData = JSON.parse(jsonData)
      setCmsData(parsedData)
      return true
    } catch (error) {
      return false
    }
  }

  // ========================
  // UPDATE FUNCTIONS
  // ========================
  const updateHeaderContent = (c: Partial<HeaderContent>) => setCmsData(p => ({ ...p, global: { ...p.global, headerContent: { ...p.global.headerContent, ...c } } }))
  const updateFooterContent = (c: Partial<FooterContent>) => setCmsData(p => ({ ...p, global: { ...p.global, footerContent: { ...p.global.footerContent, ...c } } }))
  const updatePageHeader = (pk: keyof CMSData['pages'], d: Partial<HeaderData>) => setCmsData(p => ({ ...p, pages: { ...p.pages, [pk]: { ...p.pages[pk], header: { ...p.pages[pk].header, ...d } } } }))
  const updateUpcomingEventContent = (c: Partial<UpcomingEventContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, home: { ...p.pages.home, upcomingEventContent: { ...p.pages.home.upcomingEventContent, ...c } } } }))
  const updateNewsContent = (c: Partial<NewsContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, home: { ...p.pages.home, newsContent: { ...p.pages.home.newsContent, ...c } } } }))
  const updatePrograms = (c: Partial<ProgramsContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, home: { ...p.pages.home, programsContent: { ...p.pages.home.programsContent, ...c } } } }))
  const updateSupportContent = (c: Partial<SupportContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, home: { ...p.pages.home, supportContent: { ...p.pages.home.supportContent, ...c } } } }))
  const updateScriptureContent = (c: Partial<ScriptureContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, home: { ...p.pages.home, scriptureContent: { ...p.pages.home.scriptureContent, ...c } } } }))
  const updateLoiTuBachContent = (c: Partial<LoiTuBachContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page2: { ...p.pages.page2, loiTuBachContent: { ...p.pages.page2.loiTuBachContent, ...c } } } }))
  const updateBonDaiTongChiContent = (c: Partial<BonDaiTongChiContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page2: { ...p.pages.page2, bonDaiTongChiContent: { ...p.pages.page2.bonDaiTongChiContent, ...c } } } }))
  const updateBanNhanSuContent = (c: Partial<BanNhanSuContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page2: { ...p.pages.page2, banNhanSuContent: { ...p.pages.page2.banNhanSuContent, ...c } } } }))
  const updateGhiDanhNhapHocContent = (c: Partial<GhiDanhNhapHocContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page2: { ...p.pages.page2, ghiDanhNhapHocContent: { ...p.pages.page2.ghiDanhNhapHocContent, ...c } } } }))
  const updateKhongGianThienContent = (c: Partial<KhongGianThienContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page3: { ...p.pages.page3, khongGianThienContent: { ...p.pages.page3.khongGianThienContent, ...c } } } }))
  const updateKhongGianThien12Content = (c: Partial<KhongGianThien12Content>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page3: { ...p.pages.page3, khongGianThien12Content: { ...p.pages.page3.khongGianThien12Content, ...c } } } }))
  const updateGhiDanhThienContent = (c: Partial<GhiDanhThienContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page3: { ...p.pages.page3, ghiDanhThienContent: { ...p.pages.page3.ghiDanhThienContent, ...c } } } }))
  const updateChuongTrinhSoTrungDangContent = (c: Partial<ChuongTrinhSoTrungDangContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page4: { ...p.pages.page4, chuongTrinhSoTrungDangContent: { ...p.pages.page4.chuongTrinhSoTrungDangContent, ...c } } } }))
  const updateNienKhoaContent = (c: Partial<Page4NienKhoaContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page4: { ...p.pages.page4, nienKhoaContent: { ...p.pages.page4.nienKhoaContent, ...c } } } }))
  const updateNienKhoa2022Content = (c: Partial<Page4aNienKhoa2022Content>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page4: { ...p.pages.page4, nienKhoa2022Content: { ...p.pages.page4.nienKhoa2022Content, ...c } } } }))
  const updateNienKhoa2019Content = (c: Partial<Page4bNienKhoa2019Content>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page4: { ...p.pages.page4, nienKhoa2019Content: { ...p.pages.page4.nienKhoa2019Content, ...c } } } }))
  const updateChuongTrinhCaoDangContent = (c: Partial<ChuongTrinhCaoDangContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page5: { ...p.pages.page5, chuongTrinhCaoDangContent: { ...p.pages.page5.chuongTrinhCaoDangContent, ...c } } } }))
  const updateCaoDangNienKhoaContent = (c: Partial<Page5cCaoDangNienKhoaContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page5: { ...p.pages.page5, caoDangNienKhoaContent: { ...p.pages.page5.caoDangNienKhoaContent, ...c } } } }))
  const updateCaoDang2022Content = (c: Partial<Page5eChuongTrinhCaoDangContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page5: { ...p.pages.page5, caoDang2022Content: { ...p.pages.page5.caoDang2022Content, ...c } } } }))
  const updateCaoDang2019Content = (c: Partial<Page5fChuongTrinhCaoDangContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page5: { ...p.pages.page5, caoDang2019Content: { ...p.pages.page5.caoDang2019Content, ...c } } } }))
  const updateChuyenKhoaATyDamContent = (c: Partial<Page6aChuyenKhoaATyDamContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page6: { ...p.pages.page6, chuyenKhoaATyDamContent: { ...p.pages.page6.chuyenKhoaATyDamContent, ...c } } } }))
  const updateNienKhoaContent6 = (c: Partial<Page6bNienKhoaContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page6: { ...p.pages.page6, nienKhoaContent: { ...p.pages.page6.nienKhoaContent, ...c } } } }))
  const updateProgramSectionEFContent = (c: Partial<ProgramSectionEFContent>) => setCmsData(p => ({ ...p, pages: { ...p.pages, page6: { ...p.pages.page6, programSectionEFContent: { ...p.pages.page6.programSectionEFContent, ...c } } } }))

  return (
    <CMSContext.Provider value={{
      cmsData, displayData, hasDraftChanges, currentViewMode, switchViewMode,
      resetCMSData, exportCMSData, importCMSData,
      updateHeaderContent, updateFooterContent, updatePageHeader, updateUpcomingEventContent,
      updateNewsContent, updatePrograms, updateSupportContent, updateScriptureContent,
      updateLoiTuBachContent, updateBonDaiTongChiContent, updateBanNhanSuContent, updateGhiDanhNhapHocContent,
      updateKhongGianThienContent, updateKhongGianThien12Content, updateGhiDanhThienContent,
      updateChuongTrinhSoTrungDangContent, updateNienKhoaContent, updateNienKhoa2022Content, updateNienKhoa2019Content,
      updateChuongTrinhCaoDangContent, updateCaoDangNienKhoaContent, updateCaoDang2022Content, updateCaoDang2019Content,
      updateChuyenKhoaATyDamContent, updateNienKhoaContent6, updateProgramSectionEFContent,
      saveAutosave, saveDraft, publishCMSData, discardDraft,
      hasUnsavedChanges,
      resolvedContent,
      remoteVersion,
      draftVersion,
      isSuperAdmin,
      isSuperAdminMode,
      toggleSuperAdminMode
    }}>
      {children}
    </CMSContext.Provider>
  )
}

export const useCMS = () => {
  const context = useContext(CMSContext)
  if (!context) throw new Error('useCMS must be used within a CMSProvider')
  return context
}