import React, { useEffect } from 'react';
import { useCMS } from '../../contexts/CMSContext';
import { fontVersions, GOOGLE_FONTS_URL } from '../../config/fonts';

const FontManager: React.FC = () => {
  const { displayData } = useCMS();
  // Safe access with fallback to v1
  const fontVersion = (displayData?.global?.footerContent?.fontVersion || 'v1') as keyof typeof fontVersions;
  const currentFonts = fontVersions[fontVersion];

  useEffect(() => {
    // Inject CSS variables into document root
    const root = document.documentElement;
    root.style.setProperty('--font-heading', currentFonts.heading);
    root.style.setProperty('--font-body', currentFonts.body);
    root.style.setProperty('--font-accent', currentFonts.accent);

    // Legacy variable support for components using old variable names
    root.style.setProperty('--font-serif', currentFonts.heading);
    root.style.setProperty('--font-sans', currentFonts.body);

    // Set a class or data-attribute for version-specific targeting if needed
    root.setAttribute('data-font-version', fontVersion);
  }, [currentFonts, fontVersion]);

  return (
    <React.Fragment>
      {/* Server-side & Hydration safe variables */}
      <style key="css-variables">
        {`:root {
                    --font-heading: ${currentFonts.heading};
                    --font-body: ${currentFonts.body};
                    --font-accent: ${currentFonts.accent};
                    --font-serif: ${currentFonts.heading};
                    --font-sans: ${currentFonts.body};
                }`}
      </style>

      {/* Dynamic Font Import from Google Fonts */}
      <style key="google-fonts-import">
        {`@import url('${GOOGLE_FONTS_URL}');`}
      </style>

      {/* Global CSS targeting */}
      <style key="global-font-overrides">
        {`
          body {
            font-family: var(--font-body);
          }
          
          h1, h2, h3, h4, h5, h6,
          .font-heading {
            font-family: var(--font-heading);
          }
          
          .font-accent,
          .quote-text {
            font-family: var(--font-accent);
          }
          
          /* Specific targets from request */
          /* Specific targets from request */
          .title-desktop {
            font-family: var(--font-heading) !important;
          }

          .announcement-text h4 {
            font-family: var(--font-heading) !important;
         
          }

          [data-font-version="v2"] .section-title {
             font-family: var(--font-body) !important;
             font-weight: 500 !important;
          }

          [data-font-version="v2"] .program-title {
           font-weight: 600 !important;
           font-size: 2.3rem
          }
          .support-header h2 {
             font-weight: 600 !important;
          }
           .support-header2 h2 {
           font-weight: 600 !important;
           }
          .register-title{
            font-weight: 600 !important;
          }
          .kgt-title{
            font-weight: 600 !important;
          }
          .roro-kgt-title{
            font-weight: 600 !important;
          }
          .from-register-title{
            font-weight: 600 !important;
          }
          .page5b-title{
            font-weight: 600 !important;
          }
          .pagegc-nienkhoa-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }
          .page5e-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }
          .page5f-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }

          .programef-program-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }
          .page6b-nienkhoa-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }
          .chuongtrinh-title{
            font-weight: 600 !important;
            font-size: 2.5rem
          }
            .page4-nienkhoa-title{
              font-weight: 600 !important;
              font-size: 2.5rem
            }
           .page4a-nienkhoa-title{
            font-weight: 600 !important;
            font-size: 2.3rem
          }
            .page4b-nienkhoa-title{
              font-weight: 600 !important;
              font-size: 2.3rem
            }
          a, .link-text, p, span, .subtitle {
            font-family: var(--font-body);
          }
             [data-font-version="v2"] .loi-title{
                 font-family: var(--font-accent) !important;
                 font-size: 4.5rem;
                 font-weight: 300;
             }
          /* Explicitly target sections for Allison in v2 */
          [data-font-version="v2"] .page4-nienkhoa-paragraph,
          [data-font-version="v2"] .page6b-nienkhoa-paragraph,
          [data-font-version="v2"] .pagegc-nienkhoa-paragraph,
          [data-font-version="v2"] .kgt-quote,
          [data-font-version="v2"] .roro-kgt-quote,
          [data-font-version="v2"] .loi-text p,
          [data-font-version="v2"] .quote-block p,
          [data-font-version="v2"] .loi-signature-block,
          [data-font-version="v2"] .loi-signature-name,
          [data-font-version="v2"] .quote-text {
             font-family: var(--font-accent) !important;
             font-size: 2.4rem; /* Better scale for script fonts */
          }

          /* Tablet Responsive (Allison font) */
          @media (max-width: 1024px) {
            [data-font-version="v2"] .loi-title {
              font-size: 3.5rem !important;
            }
            [data-font-version="v2"] .loi-text p,
            [data-font-version="v2"] .quote-block p,
            [data-font-version="v2"] .loi-signature-block,
            [data-font-version="v2"] .loi-signature-name,
            [data-font-version="v2"] .quote-text,
            [data-font-version="v2"] .kgt-quote,
            [data-font-version="v2"] .roro-kgt-quote {
              font-size: 2rem !important;
            }
          }

          /* Mobile Responsive (Allison font) */
          @media (max-width: 768px) {
            [data-font-version="v2"] .loi-title {
              font-size: 2.8rem !important;
            }
            [data-font-version="v2"] .loi-text p,
            [data-font-version="v2"] .quote-block p,
            [data-font-version="v2"] .loi-signature-block,
            [data-font-version="v2"] .loi-signature-name,
            [data-font-version="v2"] .quote-text,
            [data-font-version="v2"] .kgt-quote,
            [data-font-version="v2"] .roro-kgt-quote {
              font-size: 1.8rem !important;
            }
          }



          [data-font-version="v2"] .page4-nienkhoa-paragraph,
          [data-font-version="v2"] .page6b-nienkhoa-paragraph,
          [data-font-version="v2"] .pagegc-nienkhoa-paragraph{
            font-size: 2rem !important;
          }
        `}
      </style>
    </React.Fragment>
  );
};

export default FontManager;
