// @ppm-prototype--static-content @ppm-version--2.0
import { useEffect, useState } from "react";

export function useStaticConfig(
  remoteUrl?: string,
  defaults: any = {}
) {
  const isBrowser = typeof window !== "undefined";
  const [config, setConfig] = useState(defaults);

  useEffect(() => {
    if (!isBrowser) return;

    const applyConfig = (cfg: any) => {
      if (cfg && typeof cfg === "object") {
        setConfig({ ...defaults, ...cfg });
      }
    };

    // CASE A  you explicitly passed a URL
    if (remoteUrl) {
      const script = document.createElement("script");
      script.src = remoteUrl+`?sdsdf`;
      script.async = true;

      script.onload = () => {
        const remoteCfg = (window as any).MAIN_CONTENT;
        applyConfig(remoteCfg);
      };

      // if this errors, we do nothing on purpose
      document.head.appendChild(script);
      return;
    }

    // CASE B  no URL, so we ONLY look at local
    const localCfg = (window as any).MAIN_CONTENT;
    applyConfig(localCfg);
  }, [remoteUrl]);

  return isBrowser ? config : defaults;
}




export function useStaticConfigJSON(
  remoteUrl?: string,
  defaults: any = {}
) {
  const isBrowser = typeof window !== "undefined";
  const [config, setConfig] = useState(defaults);

  useEffect(() => {
    if (!isBrowser) return;

    const applyConfig = (cfg: any) => {
      if (cfg && typeof cfg === "object") {
        setConfig({ ...defaults, ...cfg });
      }
    };

    // CASE A  load a real JSON file (now with cache buster)
    if (remoteUrl) {
      const cacheBustedUrl = `${remoteUrl}?_ts=${Math.random().toString(36).slice(2)}`;

      fetch(cacheBustedUrl, { cache: "no-cache" })
        .then(res => {
          if (!res.ok) throw new Error(`HTTP ${res.status}`);
          return res.json();
        })
        .then(json => {
          applyConfig(json);
        })
        .catch(err => {
          console.warn(" useStaticConfigJSON: failed to load", err);
        });

      return;
    }

    // CASE B  no URL, fall back to window
    const localCfg = (window as any).MAIN_CONTENT;
    applyConfig(localCfg);
  }, [remoteUrl]);

  return isBrowser ? config : defaults;
}